import React, { Component } from "react";
import { withRouter } from "react-router";
import About from "@components/About/index";
import NewCourse from "../../../components/NewCourse";
import DocumentSettings from "../../../utils/seo";
class AboutScreen extends Component {
    seo = {
        title: "Hakkımda | Can Gökçeaslan",
        description: "Bu sayfada benim hakkımda daha fazla bilgiye ulaşabilirsiniz",
        keywords: "cangokceaslan, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim, Hakkımda, About Me",
        author: "Can Gökçeaslan",
        locale: "tr",
        image: "https://www.cangokceaslan.com/favicon.ico",
        siteName: "https://www.cangokceaslan.com/about"
    }
    renderNewCourse() {
        return <NewCourse />
    }
    render() {
        return (
            <React.Fragment>
                <DocumentSettings docData={this.seo} />
                <About hideTitle={true} />
                <NewCourse />
            </React.Fragment>
        )
    }
}

export default AboutScreen;